.app {
    margin: 0 auto;
    max-width: 1170px;
  }
  

li, ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.app-wrapper {
    margin: 0 auto;
    display: grid;  
    width: 1170px;
    grid-template-areas: 
    'h h'
    'n c';
    
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
    background-color: green;
}

a {
    text-decoration: none;
}

