.nav {
    grid-area: n;
    background-color: #212121;
    padding: 20px;
}

.item a {
    color: white;   
    font-size: 18px;  
    transition: .5s all;
}
.item a.active {
    color: gold;
}
.item a:hover {
    transition: .5s all;
    color: gold;
}

.nav div {
    margin-top: 20px;
}